@import url(https://fonts.googleapis.com/css?family=Montserrat:500,700,800);
body{margin:0;font-family:"Montserrat",Helvetica,Arial,sans-serif;font-size:14px;font-weight:500;line-height:123%;color:#313d5f}@media (min-width: 768px){body{font-size:16px}}*,*:before,*:after{box-sizing:border-box}html,body{height:100%;min-width:320px}#root{display:flex;flex-direction:column;justify-content:space-between;min-height:100%;overflow:hidden}.w-inner{position:relative;flex:1 1;display:flex;flex-direction:column;min-height:100%}.w-center{max-width:1200px;width:100%;margin-right:auto;margin-left:auto;padding:0 15px;flex:1 1;display:flex;flex-direction:column}.w-content{position:relative;padding:20px 0;flex:1 1}.c-header{background-color:#f7f7f7}.c-header__in{width:100%;position:relative;padding:14px 0 6px;max-width:1200px;margin:0 auto}.c-header__logo{position:relative;font-size:26px;line-height:1.5;margin:0;font-weight:800;color:#3f68a1;transform:perspective(1px) translateZ(0)}.c-header__logo:before{content:'';position:absolute;z-index:-1;top:3px;left:-6px;display:block;width:98px;height:33px;background:#fff600;transform:rotate(-3deg)}@media (min-width: 768px){.c-header__in{padding-top:25px;padding-bottom:24px}}.c-btn{position:relative;top:0;min-height:42px;display:inline-flex;justify-content:center;align-items:center;vertical-align:middle;text-decoration:none;text-align:center;font-size:17px;font-weight:800;min-height:25px;line-height:123%;padding:5px 15px;margin-bottom:3px;cursor:pointer;color:#fff;background:#336cbc;box-shadow:0 3px 0 #2256a0;border:none;border-radius:2px;transition:all .1s}.c-btn:active{background:#1b4e97;box-shadow:0 0 0 #2256a0 !important;top:3px}.c-btn:hover,.c-btn:focus{background:#2861af;outline:none}@media (min-width: 768px){.c-btn{font-size:21px;min-height:47px}}

.c-intake{margin-bottom:30px}.c-intake__title{margin-bottom:12px;font-size:24px;line-height:123%;font-weight:800;color:#3f68a1}.c-intake__col{margin:12px 0 0}.c-intake__col:first-child{margin-top:0}.c-intake__error{margin:8px 0 0;color:#fe0000;font-size:12px}.c-intake__btn-box{margin:15px 0 0;text-align:center}@media (min-width: 667px){.c-intake__box{margin:-12px -10px 0}.c-intake__col{display:inline-block;vertical-align:top;width:33.33%;padding:0 10px}.c-intake__col:first-child{margin-top:12px}}@media (min-width: 1024px){.c-intake__title{margin-bottom:18px;font-size:37px}}

.c-form-group__title{display:block;margin:0 0 11px;font-weight:700}.c-form-group__field{display:block;width:100%;height:45px;line-height:43px;background:#fff;padding:0 10px;font-family:inherit;font-size:inherit;font-weight:500;color:#222;border-radius:2px;border:2px solid #acb9d0;background:#f9f9f9;transition:all .1s}@media (min-width: 768px){.c-form-group__field{height:50px;line-height:53px;font-size:16px}}

.c-table{position:relative;padding:0 23px 23px 0;overflow:auto;font-size:20px}.c-table__in{display:table}.c-table__row{display:table-row}.c-table__row:first-child .c-table__cell{border-top:2px solid #acb9d0}.c-table__row--hide .c-table__bg{height:0 !important}.c-table__row--hide .c-table__val-default{display:block;opacity:1}.c-table__row--hide .c-table__val-aux{display:none}.c-table__row--aux{background:transparent}.c-table__cell{position:relative;display:table-cell;vertical-align:middle;text-align:center;padding:6px 15px 5px;height:50px;width:74px;border-bottom:2px solid #acb9d0;cursor:pointer}.c-table__cell:first-child{border-left:2px solid #acb9d0}.c-table__cell:nth-last-child(2){border-right:2px solid #acb9d0}.c-table__cell--aux{background:#f4f6ff;border-color:transparent !important;cursor:default}.c-table__cell--highlight{background:#dae1ff}.c-table__cell-val{position:relative;z-index:2}.c-table__val-default{opacity:0}.c-table__val-aux{position:absolute;top:0;left:0;right:0;bottom:0;display:block}.c-table__bg{position:absolute;bottom:0;left:0;right:0;height:50%;background:#dae1ff;transition:all .3s}.c-table__btn-remove{position:absolute;top:0;left:100%;width:23px;height:23px;background:#ef8686;border:none;cursor:pointer;transition:all .2s}.c-table__btn-remove:before,.c-table__btn-remove:after{content:'';position:absolute;left:50%;top:50%;margin:-7px 0 0 -2px;width:3px;height:13px;background:#fff;transition:all .3s;transform:rotate(-135deg)}.c-table__btn-remove:after{transform:rotate(-45deg)}.c-table__btn-remove:hover,.c-table__btn-remove:active,.c-table__btn-remove:focus{outline:none;background:#ff3f3f}.c-table__btn-add{position:absolute;bottom:0;left:0;width:23px;height:23px;background:#3f68a1;border:none;cursor:pointer}.c-table__btn-add:before,.c-table__btn-add:after{content:'';position:absolute;left:50%;top:50%;margin:-7px 0 0 -2px;width:3px;height:13px;background:#fff;transition:all .3s}.c-table__btn-add:after{transform:rotate(90deg)}.c-table__btn-add:hover,.c-table__btn-add:active,.c-table__btn-add:focus{outline:none;background:#2861af}

